.keyboard {
    background-color: var(--bg-kbr);
    width: inherit;
    font-size: 2.5rem;
    position: absolute;
    bottom: 0px;
}

.keyboard-row {
    display: flex;
    flex-direction: row;
}
.keyboard-row:nth-of-type(1n) {
    margin-left: 0;
    margin-right: 0;
}
.keyboard-row:nth-of-type(2n) {
    margin-left: 1rem;
    margin-right: 1rem;
}
.keyboard-row:nth-of-type(3n) {
    margin-left: 0;
    margin-right: 0;
}